export default{
	data(){
		return{
			Msglist:[],
			userInfo:{},
			// state:0未读，1已读  2：已回复
			iconList:[
				'../../static/img/weide001.png',//未读
				'../../static/img/xxzx_006.png',//已读，未回复
				'../../static/img/xxzx_009.png',//已回复
			]
		}
	},
	onShow(){
		let userInfo=uni.getStorageSync("userInfo")
		if(userInfo){
			this.userInfo=JSON.parse(userInfo)
		}
		this.getMsgList()
	},
	methods:{
		goDetails(id){
			uni.navigateTo({
				url:"./info?id="+id
			})
		},
		getMsgList(){
			this.$http.post("sMessage/list",{
				id:this.userInfo.id,
				type:1
			}).then(res=>{
				if(res.code==100){
					let list=res.info
					list.forEach((item,index)=>{
						item.createTime=this.$untils.getDate(item.createTime)
					})
					this.Msglist=list
				}
			})
		},
	}
}
